import React, { useContext } from "react";
import classes from "./LandingPage.module.css";
import Button from "../UI/Button/Button";
import Modal from "../UI/Modal/Modal";
import Backdrop from "../UI/Backdrop/Backdrop";
import { Context } from '../../context/context';
import { Redirect } from "react-router-dom";

const LandingPage = () => {
    const {showModal, openModal, closeModal, auth} = useContext(Context);
  

  let modal = showModal ? (
    <div className={classes.modalDiv}>
      <Modal visible />
      <Backdrop visible click= {closeModal}/>
    </div>
  ) : null;


  return (
    <div>
        {(!auth) ? <div className={classes.LandingPage}>
      {modal}
      <h1 className={classes.header}>
        <span>Welcome to</span> <br /> Movie Seat Booking App
      </h1>
      <p className={classes.subtitle}>Please Login to continue</p>
      <Button clicked={openModal}>Login</Button>
    </div> : <Redirect to="/home"/>}
    </div>
  );
};

export default LandingPage;
