import React, { useState, useContext, useEffect } from "react";
import { withRouter } from 'react-router-dom'
import classes from "./Modal.module.css";
import Button from "../Button/Button";
import { Context } from '../../../context/context';

const Modal = props => {
    const {closeModal, users} = useContext(Context);
    const [username, setUsername] = useState('');
    const [emptyUsername, setEmptyUsername] = useState(false);
    const [password, setPassword] = useState('');
    const [emptyPassword, setEmptyPassword] = useState(false);
    const [notFound, setNotFound] = useState(false);

    useEffect(() => {
      setEmptyUsername(false);
    }, [username]);

    useEffect(() => {
      setEmptyPassword(false);
    }, [password]);

    const submitHandler = event => {
        event.preventDefault();
        if(username === '' && password === '') {
          setEmptyUsername(true);
          setEmptyPassword(true);
          return;
        } else if (username === '') {
          setEmptyUsername(true);
          return;
        } else if (password === '') {
          setEmptyPassword(true);
          return;
        }
        users.forEach(user => {
          if(user.username === username && user.password === +password) {
            props.history.push('/home');
            closeModal();
            return;
          } else {
          }
        });
        setNotFound(true);
    }

    const usernameHandler = event => {
        setUsername(event.target.value);
        setNotFound(false);
    }

    const passwordHandler = event => {
        setPassword(event.target.value);
        setNotFound(false);
    }

    let erroMsg = (notFound) ? alert("Wrong username or password") : null;

  return (
    <div
      className={[classes.modal, props.visible ? classes.visible : null].join(
        " "
      )}>
      <h1 className={classes.heading}>Login</h1>
      <form onSubmit={submitHandler} className={classes.form}>
        <div className={classes.formControl}>
          <label htmlFor="username" className={emptyUsername ? classes.empty : null}>Username</label>
          <input onChange={usernameHandler} type="text" placeholder="Enter your username" className={ emptyUsername ? classes.emptyInput : null }/>
          <p className={[classes.blankMsg, (emptyUsername ? classes.show : null)].join(' ')}>Please enter a username</p>
        </div>
        <div className={classes.formControl}>
          <label htmlFor="password" className={emptyPassword ? classes.empty : null}>Password</label>
          <input onChange={passwordHandler} type="password" placeholder="Enter your password" className={ emptyPassword ? classes.emptyInput : null }/>
          <p className={[classes.blankMsg, (emptyPassword ? classes.show : null)].join(' ')}>Please enter a password</p>
        </div>
        <div className={classes.btns}>
          <Button name="cancel" clicked={closeModal}>Cancel</Button>
          <Button type="submit">Login</Button>
        </div>
        {erroMsg}
      </form>
    </div>
  );
};

export default withRouter(Modal);
